import React, { useEffect, useRef, useState } from 'react';
import '@/styles/RealtimeChat.css';

export default function ChatInterface({ sessionToken }: { sessionToken: string }) {
  const [messages, setMessages] = useState<any[]>([]);
  const [inputText, setInputText] = useState('');
  const [isSessionActive, setIsSessionActive] = useState(false);
  const [selectedVoice, setSelectedVoice] = useState('alloy');
  const webSocketRef = useRef<WebSocket | null>(null);
  const audioRef = useRef<HTMLAudioElement | null>(null);
  const messagesEndRef = useRef<HTMLDivElement | null>(null);
  const peerConnection = useRef<RTCPeerConnection | null>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const startSession = async () => {
    try {
      const pc = new RTCPeerConnection();
      peerConnection.current = pc;
      
      const audioEl = document.createElement("audio");
      audioEl.autoplay = true;
      pc.ontrack = e => audioEl.srcObject = e.streams[0];
      audioRef.current = audioEl;

      await navigator.mediaDevices.getUserMedia({ audio: true })
        .then(stream => {
          pc.addTrack(stream.getTracks()[0]);
        });

      const dc = pc.createDataChannel("oai-events");
      dc.addEventListener("message", handleServerEvent);

      await initializeConnection(pc, sessionToken);
      setIsSessionActive(true);
    } catch (error) {
      console.error('Failed to start session:', error);
    }
  };

  const endSession = () => {
    peerConnection.current?.close();
    if (audioRef.current) {
      audioRef.current.srcObject = null;
    }
    setIsSessionActive(false);
    setMessages([]);
    // Add a session ended message
    setMessages([{ role: 'system', content: 'SESSION ENDED' }]);
  };

  const handleServerEvent = (e: MessageEvent) => {
    const event = JSON.parse(e.data);
    switch (event.type) {
      case "response.text.delta":
        setMessages(prev => {
          const newMessages = [...prev];
          const lastMessage = newMessages[newMessages.length - 1];
          if (lastMessage?.role === 'assistant') {
            lastMessage.content += event.delta;
          } else {
            newMessages.push({
              role: 'assistant',
              content: event.delta
            });
          }
          return newMessages;
        });
        break;
    }
  };

  const handleSendMessage = (e: React.FormEvent) => {
    e.preventDefault();
    if (!inputText.trim() || !webSocketRef.current) return;

    // Add user message to chat
    setMessages(prev => [...prev, { role: 'user', content: inputText }]);

    const event = {
      type: "conversation.item.create",
      item: {
        type: "message",
        role: "user",
        content: [{
          type: "input_text",
          text: inputText
        }]
      }
    };

    webSocketRef.current.send(JSON.stringify(event));
    
    const responseEvent = {
      type: "response.create",
      response: {
        modalities: ["text", "audio"],
        voice: selectedVoice
      }
    };

    webSocketRef.current.send(JSON.stringify(responseEvent));
    setInputText('');
  };

  return (
    <div className="flex flex-col h-full">
      <div className="flex justify-between items-center p-4 border-b">
        <h1 className="text-xl font-semibold">Realtime</h1>
        <div className="flex gap-2">
          <button
            onClick={() => setMessages([])}
            className="px-3 py-1 text-gray-600 hover:bg-gray-100 rounded"
          >
            Clear
          </button>
          {!isSessionActive ? (
            <button
              onClick={startSession}
              className="px-4 py-2 bg-green-500 text-white rounded hover:bg-green-600"
            >
              Start session
            </button>
          ) : (
            <button
              onClick={endSession}
              className="px-4 py-2 bg-red-500 text-white rounded hover:bg-red-600"
            >
              End session
            </button>
          )}
        </div>
      </div>

      <div className="flex flex-1 overflow-hidden">
        <div className="realtime-chat flex-1">
          <div className="messages">
            {messages.map((msg, i) => (
              <div key={i} className={`message ${msg.role}`}>
                {msg.role === 'system' ? (
                  <div className="text-center text-gray-500 my-2">{msg.content}</div>
                ) : (
                  <div className="flex">
                    <span className="font-semibold mr-2">{msg.role.toUpperCase()}:</span>
                    <span>{msg.content}</span>
                  </div>
                )}
              </div>
            ))}
            <div ref={messagesEndRef} />
          </div>
          
          <form onSubmit={handleSendMessage} className="input-area">
            <input
              type="text"
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
              placeholder="Type your message..."
              disabled={!isSessionActive}
              className="w-full p-2 border rounded"
            />
            <button 
              type="submit" 
              disabled={!inputText.trim() || !isSessionActive}
              className="mt-2 px-4 py-2 bg-blue-500 text-white rounded disabled:bg-gray-300"
            >
              Send
            </button>
          </form>
        </div>

        <div className="settings-panel">
          <div>
            <label className="block mb-2">Voice</label>
            <select 
              value={selectedVoice}
              onChange={(e) => setSelectedVoice(e.target.value)}
              className="voice-select"
            >
              <option value="alloy">Alloy</option>
              <option value="echo">Echo</option>
              <option value="fable">Fable</option>
              <option value="onyx">Onyx</option>
              <option value="nova">Nova</option>
              <option value="shimmer">Shimmer</option>
            </select>
          </div>
        </div>
      </div>
    </div>
  );
}

async function initializeConnection(pc: RTCPeerConnection, token: string) {
  const offer = await pc.createOffer();
  await pc.setLocalDescription(offer);

  const baseUrl = "https://api.openai.com/v1/realtime";
  const model = "gpt-4o-realtime-preview-2024-12-17";
  
  const response = await fetch(`${baseUrl}?model=${model}`, {
    method: "POST",
    body: offer.sdp,
    headers: {
      Authorization: `Bearer ${token}`,
      "Content-Type": "application/sdp"
    },
  });

  const answer = {
    type: 'answer' as const,
    sdp: await response.text()
  };
  
  await pc.setRemoteDescription(answer);
}